    .characters_featureGrid__3d4MB {
        left: min(calc(1000px - 100vw) * -1, 0px);
        width: 45rem;
    }
}

.characters_featureGrid__3d4MB > a {
    border-radius: 0.75rem;
    transition: transform 350ms ease-in-out;
    overflow: hidden;
}

.characters_featureGrid__3d4MB > a:hover,
.characters_featureGrid__3d4MB > a:focus-visible {
    transform: scale(1.25);
    box-shadow: 0 0 2rem hsl(0 0% 100% / 0.8);
}

.characters_featureGrid__3d4MB > :nth-child(1),
.characters_featureGrid__3d4MB > :nth-child(4),
.characters_featureGrid__3d4MB > :nth-child(5),
.characters_featureGrid__3d4MB > :nth-child(7) {
    grid-row: span 2;
}

.characters_featureGrid__3d4MB > :nth-child(2),
.characters_featureGrid__3d4MB > :nth-child(3) {
    grid-row: span 2;
